<template>
  <div>
    <div class="block">
      <el-carousel height="100%">
        <el-carousel-item v-for="item in 4"
                          :key="item">
          <h3 class="small">{{ item }}</h3>
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>

<script>
export default {
  name: 'viewPage'
}
</script>

<style lang="scss" scoped>
.block {
  width: 100%;
  height: 314px;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  height: 100%;
  // line-height: 150px;
  margin: 0;
}

.el-carousel {
  height: 100%;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}

.el-carousel__item:nth-child(2n + 2) {
  background-color: skyblue;
}
</style>



